import React from 'react';

class Detail extends React.Component {
  componentDidMount() {
    const {location, history} = this.props;
    console.log(location);
    if (location.state === undefined) {
      history.push('/');
    }
  }
  render() {
    const {
      location: {state},
    } = this.props;
    if (state) {
      return (
        <div>
          <img src={state.poster}></img>
          <h1>{state.title}</h1>
          <h3>{state.summary}</h3>
          <ul>
            <li>{state.genres}</li>
          </ul>
          <p> created {state.year}</p>
          <p> ⭐️ {state.rating} / 5 </p>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Detail;
